import React, {useState} from "react";
import classes from "../../Common/Paginator/Paginator.module.css";

let Paginator = ({totalItemsCount, pageSize, currentPage, onPageChanged, amountItemsPerPage=10}) => {
    
    let pagesCount = Math.ceil(totalItemsCount / pageSize);
    let pagesArr = [];

    for(let i=1; i<=pagesCount; i++){
        pagesArr.push(i)
    }
    
    let amountPages = Math.ceil(totalItemsCount / amountItemsPerPage);
    let [currentSegmentOfPages, setCurrentSegmentOfPages] = useState(1);
    let leftPosition = (currentSegmentOfPages - 1) * amountItemsPerPage + 1;
    let rightPosition = currentSegmentOfPages * amountItemsPerPage;
    
    let decrement=()=>{
       return  setCurrentSegmentOfPages( currentSegmentOfPages - 1)
    }
    
    let increment=()=>{
       return setCurrentSegmentOfPages( currentSegmentOfPages + 1)
    }

    return(
        <div className={classes.pagination}>
            {<button disabled={currentSegmentOfPages > 1 ? "" : "true"}
                     onClick={decrement}> « </button>}
        
            {pagesArr.filter(page => page >= leftPosition && page <= rightPosition)
                .map((page, index) => {
                    return (
                        <span className={currentPage === page ? classes.selected : classes.item}
                              key={index}  onClick={(e) => onPageChanged(page)}>
                              { page }
                        </span>
                    )
                })}
            
            {<button disabled={amountPages > currentSegmentOfPages ? "" : "true"}
                     onClick={increment}> » </button>}
        </div>
    )
}

export default Paginator;