import React from 'react';
import styles from './Navbar.module.css';
import {NavLink} from "react-router-dom";

const Navbar = () => {
    return (
        <div className={styles.navbarWrap}>
            
            {/*Навигация*/}
            <nav className={styles.nav}>
                <ul>
                    <li>
                        <NavLink to={"/profile"} className={styles.nav_profile}
                                 activeClassName={styles.active}>
                            Profile
                        </NavLink>
                    </li>
                    <li>
                        <NavLink to={"/dialogs"} className={styles.nav_dialogs}
                                 activeClassName={styles.active}>
                            Dialogs
                        </NavLink>
                    </li>
                    {/*<li><NavLink to={"/news"} activeClassName={classes.active}>News</NavLink></li>
                    <li><NavLink to={"/music"} activeClassName={classes.active}>Music</NavLink></li>*/}
                    <li>
                        <NavLink to={"/users"} className={styles.nav_users}
                                 activeClassName={styles.active}>
                            Users
                        </NavLink>
                    </li>
                    {/*<li><NavLink to={"/settings"} activeClassName={classes.active}>Settings</NavLink></li>*/}
                </ul>
            </nav>
        </div>
    )
}
export default Navbar;