import {authAPI, securityAPI} from "../Api/Api";
import {stopSubmit} from "redux-form";

const SET_USER_DATA = 'SET-USER-DATA';
const GET_CAPTCHA_URL_SUCCESS = 'GET-CAPTCHA-URL-SUCCESS';

let initialState = {
    userId: null,
    email: null,
    login: null,
    isAuth: false,
    captchaUrl: null
};

const authReducer = (state = initialState, action) => {
    switch (action.type){
        case SET_USER_DATA:
        case GET_CAPTCHA_URL_SUCCESS:
            return  {...state, ...action.payload }
        
        default:
            return state;
    }
}

export const setAuthUserDataAC = (userId, email, login, isAuth) => ({type: SET_USER_DATA, payload: {userId, email, login, isAuth}});
export const setCaptchaUrlAC = (captchaUrl) => ({type: GET_CAPTCHA_URL_SUCCESS, payload: {captchaUrl}});

export const getAuthUserData = () => async (dispatch) => {
    const response = await authAPI.me();
    if (response.data.resultCode === 0) {
        let {email, id, login} = response.data.data
        dispatch(setAuthUserDataAC(id, email, login, true));
    }
}

export const login = (email, password, rememberMe, captcha) => {
    return async (dispatch) => {
        const response = await authAPI.login(email, password, rememberMe, captcha);
        //success, get auth data
        //error, get captcha
        if(response.data.resultCode === 0){
            dispatch(getAuthUserData());
        }else {
            if(response.data.resultCode === 10){
                dispatch(getCaptchaUrl())
            }
            let message = response.data.messages.length > 0 ? response.data.messages[0] : "Some error";
            dispatch(stopSubmit("login", {_error: message}));
        }
    }
}

export const logout = () => {
    return async (dispatch) => {
        const response = await authAPI.logout()
        if(response.data.resultCode === 0){
            dispatch(setAuthUserDataAC(null, null, null, false));
        }
    }
}

/*Captcha*/
export const getCaptchaUrl = () => {
    return async (dispatch) => {
        const captchaUrl = await securityAPI.getCaptchaUrl();
        dispatch(setCaptchaUrlAC(captchaUrl.data.url));
    }
}

export default authReducer;