import {profileAPI, usersAPI} from "../Api/Api";
import {stopSubmit} from "redux-form";

const ADD_POST = 'ADD-POST';
const SET_USER_PROFILE = 'SET-USER-PROFILE';
const SET_USER_STATUS = 'SET-USER-STATUS';
const SAVE_PHOTO_SUCCESS = 'SAVE-PHOTO-SUCCESS'

let initialState = {
    postsData: [
        {id: 1, post: 'With our simple and useful tool you can generate a standard text which you can place in your web design to look more realistic. Would you like to get new text generated for your next project?', likesCount: 3},
        {id: 2, post: 'Use the following options to generate a new lorem ipsum text for your next project', likesCount: 12},
        {id: 3, post: 'When you construct a web design you are most likely to not have any content in the beginning. ', likesCount: 5}
    ],
    profile: null
}

const profileReducer = (state = initialState, action) => {
    switch (action.type){
        case ADD_POST: {
            return  {
                ...state,
                postsData:[
                    ...state.postsData,
                    {
                        post: action.postItemText,
                        id: 4,
                        likesCount: 0
                    }
                ]
            }
        }
        
        case SET_USER_PROFILE:
            return {
                ...state,
                profile: action.profileData
            }
        
        case SET_USER_STATUS:
            return {
                ...state,
                status: action.status
            }
            
        case SAVE_PHOTO_SUCCESS:
            return {
                ...state,
                profile: {...state.profile, photos: action.photos}
            }
        
        default:
            return state;
    }
}

export let userPostActionCreator = (postItemText) => ({type: ADD_POST, postItemText});
export let setUserProfile = (profile) => ({type: SET_USER_PROFILE, profileData: profile});
export let setUserStatus = (status) => ({type: SET_USER_STATUS, status: status});
export let savePhotoSuccess = (photos) => ({type: SAVE_PHOTO_SUCCESS, photos: photos});


export let getUserProfile = (userId) => {
    return (dispatch) => {
        usersAPI.getProfile(userId)
            .then(response => {
            dispatch(setUserProfile(response.data));
        } )
    }
};
export let getUserStatus = (userId) => {
    return (dispatch) => {
        profileAPI.getStatus(userId)
            .then(response => {
            dispatch(setUserStatus(response.data));
        } )
    }
};


export let updateUserStatus = (status) => (dispatch) => {
    profileAPI.updateStatus(status)
        .then(response => {
        if(response.data.resultCode === 0){
            dispatch(setUserStatus(status));
        }
    } )
};

/*Добавить фотографию пользователя*/
export let addPhoto = (file) => (dispatch) => {
    profileAPI.savePhoto(file)
        .then(response => {
        if(response.data.resultCode === 0){
            dispatch(savePhotoSuccess(response.data.data.photos));
        }
    } )
};

/*Сохранить редактирование профиля и обновить данные */
export let saveProfile = (profile) => async (dispatch, getState) => {
    const getStates = getState().auth.userId;
    const response = await profileAPI.saveProfile(profile);
    
        if(response.data.resultCode === 0){
            dispatch(getUserProfile(getStates));
        }else{
            dispatch(stopSubmit("profileEditForm", {_error: response.data.messages[0]}));
            return Promise.reject(response.data.messages[0]);
        }
};


export default profileReducer;