import React from 'react';
import styles from './Header.module.css';
import {NavLink} from "react-router-dom";

/*Header*/
const Header = (props) => {
    return (
        <header className={styles.header}>
            {/*Logo*/}
            <div className={styles.logo}>
                <NavLink to={'/profile'}>
                    <img src="https://i.ya-webdesign.com/images/png-background-creator-8.png" alt='logo'/>
                </NavLink>
            </div>
    
            {/*Login / Logout*/}
            <div className={styles.user}>
                {props.isAuth
                    ? <div>
                        <div className={styles.user_email}> {props.email} </div>
                        <button className={styles.user_button} onClick={props.logout}>Log out</button>
                      </div>
                    : <NavLink className={styles.user_button} to={'/login'}>Login</NavLink>
                }
            </div>
        </header>
    )
}
export default Header;